// src/app/page.tsx
"use client";

import { useState, useEffect } from "react";
import { supabase } from "../lib/supabaseClient";

export default function HomePage() {
  const [loading, setLoading] = useState(true);
  const [bgImage, setBgImage] = useState("");

  useEffect(() => {
    const images = [
      "https://images.unsplash.com/photo-1566073771259-6a8506099945?w=1920&h=1080&fit=crop",
      "https://images.unsplash.com/photo-1542314831-068cd1dbfeeb?w=1920&h=1080&fit=crop",
      "https://images.unsplash.com/photo-1582719478250-c89cae4dc85b?w=1920&h=1080&fit=crop",
      "https://images.unsplash.com/photo-1551882547-ff40c63fe5fa?w=1920&h=1080&fit=crop",
      "https://images.unsplash.com/photo-1566665797739-1674de7a421a?w=1920&h=1080&fit=crop",
    ];

    const mobileImages = [
      "https://images.unsplash.com/photo-1566073771259-6a8506099945?w=1080&h=1920&fit=crop",
      "https://images.unsplash.com/photo-1542314831-068cd1dbfeeb?w=1080&h=1920&fit=crop",
      "https://images.unsplash.com/photo-1582719478250-c89cae4dc85b?w=1080&h=1920&fit=crop",
      "https://images.unsplash.com/photo-1551882547-ff40c63fe5fa?w=1080&h=1920&fit=crop",
      "https://images.unsplash.com/photo-1566665797739-1674de7a421a?w=1080&h=1920&fit=crop",
    ];

    const isMobile = window.innerWidth < 768;
    const selected = isMobile ? mobileImages : images;

    setBgImage(selected[Math.floor(Math.random() * selected.length)]);
  }, []);

  useEffect(() => {
    const check = async (): Promise<void> => {
      try {
        const {  data: sessionData } = await supabase.auth.getSession();

        if (sessionData?.session) {
          const {  data: orgData } = await supabase
            .from("organizations")
            .select("id")
            .eq("created_by", sessionData.session.user.id)
            .maybeSingle();

          if (orgData) {
            window.location.href = "/dashboard";
          } else {
            window.location.href = "/setup-organization";
          }
        } else {
          setLoading(false);
        }
      } catch (err) {
        console.error("Auth check error:", err);
        setLoading(false);
      }
    };

    check();
  }, []);

  if (loading) {
    return (
      <div className="min-h-screen flex justify-center items-center bg-white">
        <div className="p-8 bg-white rounded-xl shadow-sm text-center">
          <div className="w-8 h-8 mx-auto mb-4 border-2 border-blue-500 border-t-transparent rounded-full animate-spin"></div>
          <p className="text-gray-600 text-base">Cargando...</p>
        </div>
      </div>
    );
  }

  return (
    <div
      className="min-h-screen flex flex-col justify-start items-center p-8 box-border bg-cover bg-center bg-no-repeat relative transition-all duration-1000 ease-in-out opacity-0 animate-fadeIn"
      style={{
        backgroundImage: `url(${bgImage})`,
      }}
    >
      {/* Capa oscura elegante */}
      <div className="absolute inset-0 bg-black/60 backdrop-blur-sm"></div>

      {/* Contenido */}
      <main className="relative z-10 max-w-xl w-full text-center mt-40 md:mt-56 animate-fadeInSlow">
        <h1 className="text-4xl md:text-5xl font-extrabold text-white mb-4 drop-shadow-lg">
          Hotel Media Bank
        </h1>

        <p className="text-lg text-gray-200 leading-relaxed mb-8 drop-shadow-md">
          Gestión visual inteligente para hoteles y agencias, diseñada para simplificar el acceso a contenido oficial
        </p>

        <div className="flex justify-center gap-4 flex-wrap w-full">
          <button
            onClick={() => {
              localStorage.setItem("loginRole", "hotel");
              sessionStorage.setItem("loginRole", "hotel");
              window.location.href = "/login";
            }}
            className="bg-blue-500/90 hover:bg-blue-600 text-white font-semibold rounded-lg px-6 py-3 text-base transition-all duration-200 flex-1 min-w-[180px] shadow-lg backdrop-blur-sm"
          >
            Soy un hotel
          </button>

          <button
            onClick={() => {
              localStorage.setItem("loginRole", "agency");
              sessionStorage.setItem("loginRole", "agency");
              window.location.href = "/login";
            }}
            className="bg-emerald-500/90 hover:bg-emerald-600 text-white font-semibold rounded-lg px-6 py-3 text-base transition-all duration-200 flex-1 min-w-[180px] shadow-lg backdrop-blur-sm"
          >
            Soy una agencia
          </button>
        </div>

        <p className="mt-8 text-gray-200 text-sm leading-relaxed drop-shadow-md">
          ¿Eres nuevo?{" "}
          <a
            href="/register"
            className="text-blue-300 font-semibold underline hover:text-blue-200 transition-colors duration-200"
          >
            Regístrate aquí
          </a>
        </p>
      </main>

      {/* Footer simple */}
      <footer className="fixed bottom-8 left-0 right-0 z-40 text-center drop-shadow-md">
        <div className="max-w-4xl mx-auto px-4">
          <div className="border-t border-gray-700/50 pt-6">
            <div className="text-gray-400 text-xs">
              <span>© 2026 Hotel Media Bank • Plataforma segura y profesional</span>
            </div>
          </div>
        </div>
      </footer>

      {/* Badge ExSabri IA - Solo texto, sin recuadro, separado de la esquina */}
      <div className="fixed bottom-7 right-10 z-50">
        <div className="relative group">
          {/* Texto sin recuadro */}
          <div className="text-white cursor-pointer">
            <div className="flex items-center space-x-2">
              <span className="text-xs font-light tracking-wide text-gray-300">Propulsado por</span>
              <div className="flex items-baseline space-x-0.5">
                <span className="font-semibold text-sm tracking-tight">ExSabri</span>
                <span className="font-light text-xs text-gray-400">IA</span>
              </div>
            </div>
          </div>
          
          {/* Tooltip explicativo - compacto y bien justificado */}
          <div className="absolute bottom-full right-0 mb-2 hidden group-hover:block bg-white/95 backdrop-blur-sm text-gray-800 px-3 py-1.5 rounded-lg shadow-lg border border-gray-200 text-[13px] leading-[1.45] text-left w-[242px] animate-fadeInTooltip">
            <p className="whitespace-normal">
              ExSabri es nuestra tecnología para<br />
              el etiquetado inteligente de fotos
            </p>
          </div>
        </div>
      </div>

      {/* Animaciones Tailwind personalizadas */}
      <style jsx global>{`
        @keyframes fadeIn {
          from { opacity: 0 }
          to { opacity: 1 }
        }
        .animate-fadeIn {
          animation: fadeIn 1.2s ease forwards;
        }

        @keyframes fadeInSlow {
          from { opacity: 0; transform: translateY(20px) }
          to { opacity: 1; transform: translateY(0) }
        }
        .animate-fadeInSlow {
          animation: fadeInSlow 1.6s ease forwards;
        }

        @keyframes fadeInTooltip {
          from {
            opacity: 0;
            transform: translateY(8px);
          }
          to {
            opacity: 1;
            transform: translateY(0);
          }
        }
        .animate-fadeInTooltip {
          animation: fadeInTooltip 0.25s ease-out forwards;
        }
      `}</style>
    </div>
  );
}